<template>
  <div class="home">
    <div class="content">
      <div class="client">
        <div class="client__about">
          <h1>Клиенты</h1>
          <div class="sort">
            <p>Выберите статус:</p>
            <select
              class="status"
              v-model="selectStatus"
              @change="onChangeSelectedTask"
            >
              <option value="all" selected>Все</option>
              <option value="В обработке">В обработке</option>
              <option value="Обработан">Обработан</option>
              <option value="Отменено">Отменено</option>
            </select>
          </div>
        </div>

        <div class="client-table">
          <div class="client-table__th">
            <div class="client-table__name">
              <h2>Таблица заказов:</h2>
            </div>

            <table>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>ФИО</th>
                  <th>Email</th>
                  <th>Телефон</th>
                  <th>Город</th>
                  <th>Способ доставки</th>
                  <th>Цена заказа</th>
                  <th>Дата</th>
                  <th>Статус</th>
                  <th>Действия</th>
                </tr>
              </thead>

              <TableClients
                v-for="order in filterName"
                :key="order.id"
                v-bind:order_data="order"
                @deleteClient="deleteClient(order)"
              />
            </table>
            <div v-if="loading" class="loading">
              <div class="spin-wrapper">
                <div class="spinner"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TableClients from "@/components/table-clients.vue";

import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Clients",
  data() {
    return {
      name: "",
      phone: "",
      selectStatus: "",
      loading: false,
      sortProd: [],
    };
  },

  components: {
    TableClients,
  },
  computed: {
    ...mapGetters(["COSTUMERS"]),
    // ...mapGetters(["CONTACTS"]),
    filterName() {
      if (this.selectStatus === "all") {
        console.log("длина");
        return this.COSTUMERS;
      } else if (this.selectStatus) {
        console.log(this.selectStatus);
        return this.sortProd;
      } else {
        console.log("ничего");
        return this.COSTUMERS;
      }
    },
  },
  methods: {
    ...mapActions(["GET_COSTUMERS"]),
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
    // ...mapActions(["GET_CONTACTS"]),
    onChangeSelectedTask() {
      this.sortProd = [];
      let vm = this;
      this.COSTUMERS.map(function (item) {
        if (item.status === vm.selectStatus) {
          vm.sortProd.push(item);
        }
      });
    },
    deleteClient(order) {
      console.log(order.id);
      return axios(
        `https://xfence.ru/api/admin/v1/orders/${order.id}`,
        {
          method: "delete",
        },
        {
          headers: {
            Authorization: this.TOKEN,
            "Content-Type": "application/json",
          },
        }
      )
        .then((response) => {
          this.GET_COSTUMERS();
          console.log("Запрос прошёл успешно");
          return response;
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },
  },

  mounted() {
    this.loading = true;
    this.GET_COSTUMERS()
      .then((response) => {
        this.loading = false;
        if (response.data) {
          console.log("Data arrived");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>