<template>
  <div class="home">
    <div class="content">
      <div class="client">
        <div class="client__about">
          <h1>Обратная связь</h1>
          <div class="sort">
            <p>Выберите статус:</p>
            <select
              class="status"
              v-model="selectStatus"
              @change="onChangeSelectedTask"
            >
              <option value="all" selected>Все</option>
              <option value="В обработке">В обработке</option>
              <option value="Обработан">Обработан</option>
              <option value="Отменено">Отменено</option>
            </select>
          </div>
        </div>
        <div class="client-table">
          <div class="client-table__th">
            <div class="client-table__name">
              <h2>Заявки на обратную связь:</h2>
            </div>
            <table>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>ФИО</th>
                  <th>Телефон</th>
                  <th>Дата</th>
                  <th>Статус</th>
                  <th>Действия</th>
                </tr>
              </thead>
              <TableFeedback
                v-for="app in filterName"
                :key="app.id"
                v-bind:app_data="app"
                @deleteClient="deleteClient(app)"
              />
            </table>
            <div v-if="loading" class="loading">
              <div class="spin-wrapper">
                <div class="spinner"></div>
              </div>
            </div>
            <!-- <TableNone v-if="!this.sortProd && this.selectStatus" /> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import TableFeedback from "@/components/table-feedback.vue";
import TableNone from "@/components/Table-none.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "table-feedback",
  data() {
    return {
      selectStatus: "",
      selected: "Все",
      sortProd: [],
      loading: false,
    };
  },
  components: {
    TableFeedback,
    TableNone,
  },
  computed: {
    ...mapGetters(["CONTACTS"]),
    filterName() {
      if (this.selectStatus === "all") {
        console.log("длина");
        return this.CONTACTS;
      } else if (this.selectStatus) {
        console.log(this.selectStatus);
        return this.sortProd;
      } else {
        console.log("ничего");
        return this.CONTACTS;
      }
    },
  },
  methods: {
    ...mapActions(["GET_CONTACTS"]),
    onChangeSelectedTask() {
      this.sortProd = [];
      let vm = this;
      this.CONTACTS.map(function (item) {
        if (item.status === vm.selectStatus) {
          vm.sortProd.push(item);
        }
      });
    },
    deleteClient(app) {
      console.log(app.id);
      return axios(
        `https://xfence.ru/api/admin/v1/contacts/${app.id}`,
        {
          method: "delete",
        },
        {
          headers: {
            Authorization: this.TOKEN,
            "Content-Type": "application/json",
          },
        }
      )
        .then((response) => {
          this.GET_CONTACTS();
          console.log("Запрос прошёл успешно");
          return response;
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },
  },
  mounted() {
    this.loading = true;
    this.GET_CONTACTS()

      .then((response) => {
        this.loading = false;
        if (response.data) {
          console.log("Data arrived");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>

